@charset "UTF-8";
:local(.markdownContainer) h1 {
  font-size: 1.75em; }

:local(.markdownContainer) h2 {
  font-size: 1.6em; }

:local(.markdownContainer) h3 {
  font-size: 1.5em; }

:local(.markdownContainer) h4 {
  font-size: 1.4em; }

:local(.markdownContainer) h5 {
  font-size: 1.3em; }

:local(.markdownContainer) h6 {
  font-size: 1.25em; }

:local(.markdownContainer) a {
  text-decoration: none; }

:local(.markdownContainer) code {
  background-color: #E0E0E0;
  color: #000000;
  padding: 2px;
  border-radius: 2px; }

:local(.markdownContainer) pre {
  border-radius: 2px; }
  :local(.markdownContainer) pre code {
    color: #FFFFFF;
    background-color: #282A36;
    padding: 1em; }

:local(.markdownContainer) blockquote {
  background: #F9F9F9;
  border-left: 10px solid #CCCCCC;
  margin: 1.5em 10px;
  padding: 0.5em;
  quotes: "" "" "" ""; }
  :local(.markdownContainer) blockquote p {
    display: inline; }

:local(.markdownContainer) table {
  display: block;
  overflow-x: auto;
  white-space: nowrap; }
  :local(.markdownContainer) table tr {
    border-bottom-color: #CCCCCC;
    border-bottom-width: 1px;
    border-bottom-style: solid; }
  :local(.markdownContainer) table tr:last-child {
    border-bottom: none; }
  :local(.markdownContainer) table th, :local(.markdownContainer) table td {
    padding: 1em; }
